#home {
  position: relative;
}

.app__header {
  padding-top: 6rem;

  .app__header-div {
    display: flex;
    align-items: center;

    .app__header-info {
      flex: 1;

      .app__header-greeting {
        width: fit-content;
        margin: auto;

        font-size: x-large;
        font-weight: bolder;
        background-color: #e4e4e4;
        border-radius: 15%;
        padding: 3rem;

        .app__header-name {
          font-size: 3rem;
        }

        .app__header-profile {
          font-size: 1.35rem;
          color: #5e5e5e;
        }

        .app__header-icons {
          display: flex;
          justify-content: space-around;

          margin-top: 1rem;

          .app__header-web-icon {
            color: red;
          }

          .app__header-android-icon {
            color: #3ddc84;
          }
        }

        @media screen and (max-width: 600px) {
          background-color: var(--primary-color);
        }
      }

      .app__header-social-profiles {
        display: flex;
        flex-direction: column;
        align-items: center;

        width: fit-content;
        margin: auto;

        padding: 2rem;

        @media screen and (max-width: 600px) {
          display: none;
        }
      }
    }

    .header__image-div {
      flex: 1;

      .header__image {
        width: 100%;

        @media screen and (max-width: 600px) {
          width: 88%;
        }
      }

      @media screen and (max-width: 600px) {
        display: contents;
      }
    }

    .app__header-empty {
      flex: 1;
    }

    @media screen and (max-width: 600px) {
      flex-direction: column;
    }
  }

  @media screen and (min-width: 901px) and (max-width: 1024px) {
    padding-top: 8rem;
  }

  @media screen and (min-width: 601px) and (max-width: 900px) {
  }

  @media screen and (max-width: 600px) {
    // padding-top: 3rem;
  }
}
